import { Component, OnInit, Input } from "@angular/core";
import { Product } from "../../../shared/models/product";
import { AuthService } from "../../../shared/services/auth.service";
import { ProductService } from "../../../shared/services/product.service";
import { ToastrService } from "src/app/shared/services/toastr.service";
import { FileUpload } from "src/app/shared/services/fileupload";
import { UploadFileService } from "src/app/shared/services/upload-file.service";
import { map } from "rxjs/operators";
@Component({
  selector: "app-product-list",
  templateUrl: "./product-list.component.html",
  styleUrls: [ "./product-list.component.scss" ]
})
export class ProductListComponent implements OnInit {
  @Input() fileUpload: FileUpload;
  productList: Product[];
  loading = false;
  brands = [ "All", "Google", "Apple", "Samsung", "OnePlus", "Lenovo", "Nokia", "Motorolla" ];

  selectedBrand: "All";
  fileUploads: any[];

  page = 1;
  constructor(
    public authService: AuthService,
    private productService: ProductService,
    private toastrService: ToastrService,
    private uploadService: UploadFileService
  ) {}

  ngOnInit() {
    this.getAllProducts();
    this.uploadService.getFileUploads(6).snapshotChanges().pipe( changes => {
      return changes.pipe( map( (c: any) => ({ key: c.payload.key, ...c.payload.val() })));
    }).subscribe(fileUploads => {
      this.fileUploads = fileUploads.json();
      // console.log(fileUploads.json());
    });
  }
  deleteFileUpload(fileUpload) {
    this.uploadService.deleteFileUpload(fileUpload);
  }
  getAllProducts() {
    // this.spinnerService.show();
    this.loading = true;
    const x = this.productService.getProducts();
    x.snapshotChanges().subscribe(
    (product) => {
      this.loading = false;
      // this.spinnerService.hide();
      this.productList = [];
      product.forEach((element) => {
          // console.log(element);
          const y = element.payload.toJSON();
          console.log(y);
          y["$key"] = element.key;
          this.productList.push(y as Product);
          // console.log(this.productList);
        });
      },
      (err) => {
        this.toastrService.error("Error while fetching Products", err);
      }
    );
  }

  removeProduct(key: string) {
    this.productService.deleteProduct(key);
  }

  addFavourite(product: Product) {
    this.productService.addFavouriteProduct(product);
  }

  addToCart(product: Product) {
    this.productService.addToCart(product);
  }
}
